'use strict';

// 1. 선택한 값을 localStorage에 저장
// 2. 새로 고침하면 저장된 값을 로드


const select = document.querySelector(".js-select");
const option = select.querySelector("option");

const COUNTRY_LS = 'country';
const SHOW_CTR = 'option';
// select 저장
function saveCountry() {

    // select의 값을 selectCTR에 넣고
    const selectCTR = select.value;

    // 로컬 스토리지에 key를 country로 저장
    localStorage.setItem(COUNTRY_LS, selectCTR);
    console.log(selectCTR);

}

function loadCountry() {
    // 로컬 스토리지에 key 가 country인 값을 savedCTR에 넣음
    const savedCTR = localStorage.getItem(COUNTRY_LS);
    console.log(savedCTR);
    select.addEventListener('change', saveCountry);
    if (savedCTR !== null) {
        select.value = savedCTR;
    }
}
function init() {
    loadCountry();
}
init();